<template>
  <div class="holder">

  </div>
</template>

<script>
import axios from 'axios';
import { required } from 'vuelidate/lib/validators';
import { mapActions, mapGetters } from 'vuex';

import variables from '../../assets/common';
import authRequestMixin from '../../mixins/authRequestMixin';

export default {
  name: 'Address',
  mixins: [authRequestMixin],
  computed: {
    ...mapGetters(['getCheckoutAddress']),
  },
  data: () => ({
    isModalVisible: false,
    addrId: null,
    addrData: {},
    addressesList: [],
  }),
  validations() {
    if (!this.addrData.billingAsShipping) {
      return {
        addrData: {
          billing: { required },
          shipping: { required },
        },
      };
    }

    return {
      addrData: {
        billing: { required },
      },
    };
  },
  created() {
    // this.customerAddresses();
    // this.addrData = this.getCheckoutAddress;
  },
  methods: {
    ...mapActions(['addCheckoutAddress']),
    openModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.addrId = null;
      this.isModalVisible = false;
      this.updateFullAddress();
    },
    openModalById(id) {
      this.addrId = id;
      this.isModalVisible = true;
    },
    updateFullAddress() {
      axios
        .get(`${variables.server}/addresses`, this.authHeader)
        .then((response) => {
          this.addressesList = response.data.data;
          this.addrData = this.getCheckoutAddress;
          this.setFullAddress('billing', this.addrData.billing);
          this.setFullAddress('shipping', this.addrData.shipping);
          this.addCheckoutAddress(this.addrData);
        })
        .catch((e) => {
          this.errorHandler('On get addresses', e);
        });
    },
    customerAddresses() {
      axios
        .get(`${variables.server}/addresses`, this.authHeader)
        .then((response) => {
          this.addressesList = response.data.data;

          if (!this.getCheckoutAddress.billing) {
            this.setDefaultAddresses();
          }
        })
        .catch((e) => {
          this.errorHandler('On get addresses', e);
        });
    },
    setDefaultAddresses() {
      this.addressesList.forEach((item) => {
        if (item.attributes.isDefaultBilling === true && item.attributes.isDefaultShipping === true) {
          this.addrData.billingAsShipping = true;
        }

        if (item.attributes.isDefaultBilling) {
          this.addrData.billing = item.id;
        }

        if (item.attributes.isDefaultShipping) {
          this.addrData.shipping = item.id;
        }
      });

      this.addCheckoutAddress(this.addrData);
    },
    getValidationClass(fieldName) {
      const field = this.$v.addrData[fieldName];

      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty,
        };
      }

      return false;
    },
    validate() {
      this.$v.addrData.$touch();

      if (!this.$v.addrData.$invalid) {
        this.setData();
      }
    },
    setData() {
      if (this.addrData.billingAsShipping) {
        this.addrData.shipping = this.addrData.billing;
      }

      this.addCheckoutAddress(this.addrData);
      this.setDone();
    },
    setDone() {
      this.$emit('setDone');
    },
    getOptionText(address) {
      const addressText = [
        this.$t(`checkout.${address.attributes.salutation}`),
        address.attributes.firstname,
        address.attributes.lastname,
      ];

      if (address.attributes.address1) {
        addressText.push(`${address.attributes.address1}, `);
      }

      if (address.attributes.address2) {
        addressText.push(`${address.attributes.address2}, `);
      }

      if (address.attributes.zip) {
        addressText.push(`${address.attributes.zip}, `);
      }

      addressText.push(this.$t(`checkout.${address.attributes.country.iso}`));

      return addressText.join(' ');
    },

    setFullAddress(type, id) {
      const found = this.addressesList.find(item => item.id === id);

      this.addrData[`${type}FullAddress`] = this.getOptionText(found);
      this.addCheckoutAddress(this.addrData);
    },
    onClickAnalytic() {
      this.$gtag.event('Checkout', {
        event_category: 'Button',
        event_label: 'ZurZusammenfassung',
        value: 1,
      });
    },
  },
};
</script>
